$prim-clr: #0D3B66;
$alt-clr: #9a031e;
$alt-clr2: #5f0f40;
$alt-clr3: #fb8b24;
$alt-clr4: #e36414;
/*##############################CSS##########################*/
* {
  margin: 0;
  padding: 0;
  border-box: 0; }

#gameContainer {
  background-image: url('./assets/brainBackground.png');
  background-size: 100%;
  background-position: 50% 50%;
  display: grid;
  grid-template: .8fr 3fr / 1fr 2fr;
  height: 100vh;
  position: relative;
  overflow: hidden;
  width: 100%;
  z-index: 1;
  &::before {
    content: '';
    height: 100%;
    opacity: 0.5;
    position: absolute;
    width: 100%;
    z-index: -1; } }
/*##############################GRID LAYOUTS#############################*/
#titleContainer {
  display: flex;
  grid-column: 1/3;
  grid-row: 1/2;
  height: 100%;
  width: 100%; }

#playerBtnContainer {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-column: 1/2;
  grid-row: 2/4;
  height: 90%;
  justify-content: center;
  margin: auto;
  width: 100%; }

#nameEntryContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-column: 2/3;
  grid-row: 2/3;
  height: 90%;
  margin: auto 0; }

#menuContainer {
  animation: slide-in-right 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  content: '';
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 15vh 7fr;
  height: 100%;
  transition: all .5s ease-out;
  width: 100%;
  position: absolute;
  overflow: hidden;
  z-index: 1;
  &::before {
    content: '';
    background: #A0A0A0;
    height: 100%;
    opacity: 0.9;
    position: absolute;
    width: 100%;
    z-index: -1; }
  .menuCard {
    display: inline-block;
    grid-row: 1/2;
    margin: auto;
    transition: all .5s ease-out;
    width: 90%;
    &:hover {
      transform: scale(1.10); } } }

/*########################################MENU##################################################*/

#menuBtnContainer {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  grid-column: 1/4;
  height: 95%;
  justify-content: space-between;
  margin: auto;
  width: 90%;
  .menuBtn {
    -webkit-animation: slide-in-blurred-bottom 0.6s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
    animation: slide-in-blurred-bottom 0.6s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
    background: $prim-clr;
    box-shadow: -5px -5px 10px black;
    cursor: context-menu;
    display: inline-block;
    padding: 20px 0;
    position: relative;
    margin: 10px;
    max-height: 100px;
    overflow: hidden;
    transition: all .5s ease-out;
    width: 25%;
    white-space: nowrap;
    &::before {
      background: linear-gradient(145deg, rgba(251,139,36,1)0%,  rgba(251,139,36, 1)9%, rgba(13,59,102, 0)10%, rgba(13,59,102, 0)90%, rgba(251,139,36, 1)91%, rgba(251,139,36, 1));
      content: '';
      height: 100%;
      left: 0;
      top: 0;
      position: absolute;
      width: 100%; }
    div {
      background: white;
      display: flex;
      height: 100%;
      margin: auto;
      padding: 1px 0;
      text-align: center;
      width: 100%;
      span {
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 2px;
        margin: auto; } }
    &:hover {
      border-radius: 10px;
      transform: scale(1.10); } } }



.menu {
  background: white;
  box-shadow: -5px -5px 10px black;
  cursor: context-menu;
  display: flex;
  padding: 12px 0;
  grid-row: 1/2;
  height: 100%;
  margin: auto;
  width: 90%;
  p {
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 2px;
    margin: auto; } }


#menuConfirm {
  animation: slide-in-right 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  background: url(./assets/brainBackground.png);
  background-size: cover;
  background-position: 50% 50%;
  content: '';
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 15vh 1fr 15vh;
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: 2; }

#goBack {
  background: $alt-clr4;
  box-shadow: -5px -5px 10px black;
  color: white;
  display: flex;
  grid-row: 1/2;
  grid-column: 1/2;
  height: 80%;
  margin: auto auto auto 1em;
