    font-weight: 700;
    margin: 0 auto auto auto;
    text-align: center;
    max-height: 15%;
    width: 100%; } }


#confirmOptions {
  align-items: center;
  box-shadow: -5px -5px 10px black;
  display: flex;
  flex-direction: column;
  grid-column: 2/3;
  grid-row: 2/3;
  height: 100%;
  justify-content: center;
  margin: auto auto auto 1em;
  width: 80%;
  p {
    background: white;
    border: solid 2px black;
    border-left: none;
    border-right: none;
    padding-top: 30px;
    height: 15%;
    font-size: 24px;
    font-weight: 700;
    margin: 0 auto auto auto;
    text-align: center;
    max-height: 15%;
    width: 100%; } }

.confirmCard {
  background: $alt-clr;
  box-shadow: -5px -5px 10px black;
  display: flex;
  height: 15%;
