  overflow: hidden;
  transition: all .5s ease-out;
  width: 50%;
  &:hover {
    background: 'green';
    border-radius: 10px;
    transform: scale(1.10); }
  span {
    background: white;
    font-size: 20px;
    font-weight: 700;
    height: 50%;
    letter-spacing: 2px;
    margin: auto;
    padding-top: 10px;
    text-align: center;
    width: 100%; }
  &::before {
    background: linear-gradient(145deg, rgba(251,139,36,1)0%,  rgba(251,139,36, 1)9%, rgba(13,59,102, 0)10%, rgba(13,59,102, 0)90%, rgba(251,139,36, 1)91%, rgba(251,139,36, 1));
    content: '';
    position: absolute;
    height: 100%;
    width: 100%; } }

/*##################################GAME ####################################*/

#game {
