  grid-template-rows: 20vh 1fr 1fr;
  grid-column: 1/3;
  grid-row: 1/3;
  #title {
    grid-row: 2/3;
    height: 90%; } }

#scoreBoard {
  align-items: center;
  box-shadow: -5px -5px 15px black;
  display: flex;
  flex-wrap: wrap;
  grid-row: 1/2;
  height: 100%;
  justify-content: space-around;
