  position: relative;
  overflow: hidden;
  width: 40%;
  &:hover {
    background: green;
    border-radius: 10px;
    transform: scale(1.10); }
  div {
    background: white;
    display: flex;
    height: 75%;
    margin: auto;
    width: 100%;
    span {
      font-size: 20px;
      font-weight: 700;
      letter-spacing: 2px;
      margin: auto; } }
  &::before {
    background: linear-gradient(145deg, rgba(251,139,36,1)0%,  rgba(251,139,36, 1)9%, rgba(13,59,102, 0)10%, rgba(13,59,102, 0)90%, rgba(251,139,36, 1)91%, rgba(251,139,36, 1));
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%; } }

#gameOver {
  background: $prim-clr;
  box-shadow: -5px -5px 15px black;
  height: 50%;
  left: 10%;
  position: absolute;
  overflow: hidden;
  top: 25%;
  width: 80%;
  z-index: 1; }

.gameOverCard {
  box-shadow: -5px -5px 15px black;
  display: flex;
  flex-direction: column;
  div {
    background: #E0E0E0;
    display: flex;
    height: 50%;
    margin: auto;
    width: 100%;
    p {
      color: black;
      font-size: 48px;
      font-weight: 700;
      letter-spacing: 2px;
      margin: auto;
      text-align: center; } } }


#gameRestart {
  align-items: center;
  background: green;
  box-shadow: -5px -5px 10px black;
  display: flex;
  grid-column: 1/3;
  grid-row: 3/4;
  height: 50%;
  margin: auto auto 1em auto;
  position: relative;
  overflow: hidden;
  transition: all .5s ease-out;
  width: 50%;
  &:hover {
    background: 'green';
    border-radius: 10px;
    transform: scale(1.10); }
  div {
    background: white;
    display: flex;
    font-size: 20px;
    font-weight: 700;
    height: 50%;
    letter-spacing: 2px;
    margin: auto;
    text-align: center;
    width: 100%;
    span {
      margin: auto; } }
  &::before {
    background: linear-gradient(145deg, rgba(251,139,36,1)0%,  rgba(251,139,36, 1)9%, rgba(13,59,102, 0)10%, rgba(13,59,102, 0)90%, rgba(251,139,36, 1)91%, rgba(251,139,36, 1));
    content: '';
    position: absolute;
    height: 100%;
    width: 100%; } }



/*#################################CARD STYLES ############################*/

.cardContainer {
  background: $prim-clr;
  display: inline-block; }

.card {
  background: linear-gradient(145deg, rgba(251,139,36,1)0%,  rgba(251,139,36, 1)9%, rgba(13,59,102, 0)10%, rgba(13,59,102, 0)90%, rgba(251,139,36, 1)91%, rgba(251,139,36, 1));
  color: white;
  display: flex;
  height: 100%;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 1;
  &::before {
    background: white;
    border-top: 5px white solid;
    content: '';
    height: 5%;
    bottom: 3%;
    transform: rotate(145deg);
    right: -5%;
    position: absolute;
    width: 20%;
    z-index: -1; }
  &::after {
    background: white;
    border-top: 5px white solid;
    content: '';
    height: 5%;
    top: 3%;
    transform: rotate(145deg);
    left: -5%;
    position: absolute;
    width: 20%;
    z-index: -1; }
  p {
    font-size: 200%;
    letter-spacing: 2px;
    margin: auto; } }

#title {
  -webkit-animation: swing-in-top-fwd 1s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
  animation: swing-in-top-fwd 1s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
  box-shadow: -5px -5px 10px black;
  height: 100%;
  margin: 1em auto 0 auto;
  width: 90%; }


.nameInput {
  -webkit-animation: slide-in-right 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
  animation: slide-in-right 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) forwards;
  background: $alt-clr2;
  box-shadow: -5px -5px 10px black;
  height: 22%;
  margin: auto;
  width: 80%; }

.nameForm {
  color: black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  margin: auto;
  label {
    color: white;
    font-size: 125%;
    grid-column: 2/3;
    grid-row: 1/2;
    padding-bottom: .5em;
    margin: auto; }
  input {
    border: none;
    font-size: 125%;
    grid-column: 2/3;
    grid-row: 2/3;
    height: 1.5em;
    padding: 0 16px;
    transition: all 1s linear;
    margin: auto;
    width: 80%;
    &:focus {
      -webkit-animation: color-change-3x 4s linear infinite alternate both;
      animation: color-change-3x 4s linear infinite alternate both; } }
  div {
    background-image: url('./assets/cMark.png');
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 100%;
    grid-column: 3/4;
    grid-row: 1/3;
    height: 100%;
    margin: auto;
    width: 50px; } }



/*########################END OF CARDS ##################################*/
